	module.exports = {"data":{"wordpressPost":{"title":"The impact of economic austerity, sleep and electronics and abortion leglisation: Most Read Articles in February","content":"<p>February&#8217;s Top 10 Most Read includes a selection of new entries, including a time-series analysis on the impact of economic austerity on suicide in Greece written by Branas <em>et al and </em>Hysing <em>et al</em>.&#8217;s population-based study on sleep and the use of electronic devices. Articles which are still proving to be popular include Petrilli <em>et al</em>.&#8217;s article on the role of physician attire on patient perception, and Bourne <em>et al.</em>&#8216;s cross sectional survey on the impact of complaints procedures on the welfare, health and clinical practice of UK doctors.</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Branas <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e005619.full?sid=7b3ceb1b-5350-43c7-b7dd-5b5f0f3cc048\">The impact of economic austerity and prosperity events on suicide in Greece: a 30-year interrupted time-series analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Hysing <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e006748.full\">Sleep and use of electronic devices in adolescence: results from a large population-based study</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Petrilli <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e006578.full\">Understanding the role of physician attire on patient perceptions: a systematic review of the literature— targeting attire to improve likelihood of rapport (TAILOR) investigators</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Deane <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e006434.full\">Priority setting partnership to identify the top 10 research priorities for the management of Parkinson&#8217;s disease</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Esposito <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/2/e005892.full\">A nomogram to estimate the HbA1c response to different DPP-4 inhibitors in type 2 diabetes: a systematic review and meta-analysis of 98 trials with 24 163 patients</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Bourne <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e006687.full\">The impact of complaints procedures on the welfare, health and clinical practise of 7926 doctors in the UK: a cross-sectional survey</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Hubble <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e006095.full\">Trunk muscle exercises as a means of improving postural stability in people with Parkinson&#8217;s disease: a protocol for a randomised controlled trial</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Parand <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/9/e005055.full\">The role of hospital managers in quality and patient safety: a systematic review</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Rah <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/2/e005180.full\">Household sanitation and personal hygiene practices are associated with child stunting in rural India: a cross-sectional analysis of surveys</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Koch <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/2/e006013.full\">Abortion legislation, maternal healthcare, fertility, female literacy, sanitation, violence against women and maternal deaths: a natural experiment in 32 Mexican states</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"bd296d55-fbd4-5e47-ae0e-0b3543d81dd8"}}

